import { AddIcon } from '@chakra-ui/icons';
import { Box, Button, Stack, Text } from '@chakra-ui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { getSender } from '../../config/ChatLogics';
import { ChatState } from '../../Context/ChatProvider'
import ChatLoading from '../ChatLoading';
import GroupChatModal from './miscellaneous/GroupChatModal';

const MyChats = ({fetchAgain}) => {
  const [loggedUser, setLoggedUser] = useState();
  
  const {selectedChat, setSelectedChat, user, Chats, setChats} = ChatState();

  const toast = useState();

  const fetchChats = async () => {
    try {
      const config = {
        headers: {
          Authorization: `Bearer ${user.token}`,
        },
      };

      const { data } = await axios.get('/api/chat', config);
      setChats(data);
      console.log(data);
    } catch (error) {
      toast({
        title: "Error Occured!",
        discription: "failed to Load the chats",
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom-left",
      });
    }
  }

  useEffect(() => {
    setLoggedUser(JSON.parse(localStorage.getItem("userInfo")));
    fetchChats();
  }, [fetchAgain]);
  
  return <Box
    display={{ base: selectedChat ? "none" : "flex", md: "flex" }}
    flexDir="column" alignItems={"center"} 
    p={3} bg="white" w={{ base: "100%", md: "31%" }}
    borderRadius='lg' borderWidth={'1px'}
  >
    <Box
      pb={3} px={3} fontSize={{ base: "28px", md: "30px" }}
      display="flex" w="100%" justifyContent={"space-between"}
      alignItems="center"
    >
      My Chats
      <GroupChatModal>
      <Button
        display={'flex'} fontSize={{ base: "17px", md: "10px", lg: "17px" }}
        rightIcon={<AddIcon/>}
      >
        New Group Chat
        </Button>
        </GroupChatModal>
    </Box>

    <Box
    display={ "flex" }
    flexDir="column" 
    p={3} bg="#F8F8F8" w={"100%"}
    borderRadius='lg' overflowY={'hidden'}
    >
      {Chats ? (
        <Stack overflowY={"scroll"}>
          {Chats.map((chat) => (
            <Box
              onClick={() => setSelectedChat(chat)}
              cursor="pointer" bg={selectedChat === chat ? "#F8F8F8" : "#E8E8E8"}
              color={selectedChat === chat ? "white" : "black"}
              px={3} py={2} borderRadius="lg" 
              key={chat._id}
            >
              <Text>
                {!chat.isGroupChat ? getSender(loggedUser,chat.users):chat.chatName}
              </Text>
              </Box>
            ))}
        </Stack>
      ) : (
         <ChatLoading/>
      )}
    </Box>
  </Box>
}

export default MyChats
